:root {
  --ra-color-primary: #17479e;
  --ra-color-blue: 2, 44, 130;
  --ra-color-white: #ffffff;
  --ra-color-white-2: #fafafa;
  --ra-color-black: #000000;
  --ra-color-black-2: #363535;
  --ra-color-black-3: #1a2b3d;
  --ra-color-gray-1: #333333;
  --ra-color-gray-2: #4f4f4f;
  --ra-color-gray-3: #f5f5f5;
  --ra-color-gray-4: #f8f8f8;
  --ra-color-gray-5: #e0e0e0;
  --ra-color-gray-6: #efeff4;
  --ra-color-gray-7: #8e8e93;
  --ra-color-gray-8: #bdbdbd;
  --ra-color-gray-light: #f2f2f2;
  --ra-color-gray-dark: #535e76;
  --ra-color-red-dark: #ff0000;
  --ra-color-red: #ef5a50;
  --ra-color-green: #008000;
  --ra-font-family: "Poppins", sans-serif !important;
}

/* 
- Media queries should use em unite instead of rem or px
- Bootstrap breakpoint added
- 1em = 16px
- 479px x 16 = 29.94em -> smallest
- 500px x 16 = 31.25em -> smaller
- 575px x 16 = 35.94em -> small
- 767px x 16 = 47.94em -> medium
- 991px x 16 = 61.94em -> large
- 1199px x 16 = 74.94em -> extraLarge
- 1399px x 16 = 87.44em -> extraExtraLarge
*/
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}
@media screen and (max-width: 61.94em) {
  html {
    font-size: 55%;
  }
}
@media screen and (max-width: 47.94em) {
  html {
    font-size: 52%;
  }
}

body {
  font-size: 1.6rem;
  line-height: 2.4rem;
  box-sizing: border-box;
  font-family: var(--ra-font-family);
}

